.profile {
  background-color: rgb(205, 211, 230);
  display: flex;
  justify-content: center;
  height: 700px;
}

.avatar {
  background-color: rgb(205, 211, 230);
  border-radius: 50%;
  width: 200px;
}

.profile-content {
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin-top: 50px;
  width: 400px;
  height: 550px;
  background-color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.name {
  color: black;
  font-size: 28px;
  margin-bottom: 0px;
  font-weight: 700px;
}

.tag {
  margin-top: 10px;
  color: rgb(128, 126, 126);
  font-size: 20px;
  margin-bottom: 0px;
  font-weight: 700px;
}

.location {
  margin-top: 10px;
  color: rgb(128, 126, 126);
  font-size: 20px;
  margin-bottom: 0px;
  font-weight: 700px;
}

.stats {
  gap: 2px;
  background-color: rgb(222, 230, 245);
  margin-top: 40px;
  align-items: center;
  justify-content: center;
  display: flex;
  list-style: none;
  padding: 0;
  margin-bottom: 0px;
  border-radius: 10px;
}

.stats-item {
  justify-content: center;
  align-items: center;
  height: 85px;
  width: 132px;
  display: flex;
  flex-direction: column;
}

.label {
  color: black;
  font-size: 16px;
  margin-bottom: 4px;
}

.quantity {
  color: black;
  font-size: 20px;
}
