.statistics {
  background-color: rgb(179, 185, 206);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.statisticsContent {
  margin-top: 65px;
  width: auto;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-bottom: 55px;
  border-radius: 10px;
}

.title {
  font-size: 40px;
}

.statList {
  margin: 0px;
  flex-wrap: wrap;
  padding: 0;
  display: flex;
  list-style: none;
}

.item {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 35px;
  padding-right: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.label {
  font-size: 18px;
  color: white;
}

.percentage {
  font-size: 40px;
  color: white;
}
